import React, {useState} from "react";
import "./index.scss";
import user from "../../../assets/user.jpg";
import { ReactComponent as Twitter } from "../../../assets/icons/twitter.svg";
import { ReactComponent as Home } from "../../../assets/icons/home.svg";
import { ReactComponent as Explore } from "../../../assets/icons/hash.svg";
import { ReactComponent as Notification } from "../../../assets/icons/notification.svg";
import { ReactComponent as Message } from "../../../assets/icons/list-text.svg";
import { ReactComponent as Bookmark } from "../../../assets/icons/small-bookmark.svg";
import { ReactComponent as List } from "../../../assets/icons/list-text.svg";
import { ReactComponent as Profile } from "../../../assets/icons/user.svg";
import { ReactComponent as More } from "../../../assets/icons/recycle.svg";

const Index = () => {
  const [isOpen, setisOpen] = useState(false)
  return (
    <div className="left-menu">
      <div className="twitter-icon">
        <Twitter className="t-icon" />
      </div>
      <div className="menu-links">
        <ul className="link-group">
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={home} alt=""/> */}
              <Home className="link-svg" />
            </div>
            <a href="#" className="link">
              Home
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={explore} alt=""/> */}
              <Explore className="link-svg" />
            </div>
            <a href="#" className="link">
              Explore
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={notifications} alt=""/> */}
              <Notification className="link-svg" />
            </div>
            <a href="#" className="link">
              Notifications
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={message} alt=""/> */}
              <Message className="link-svg" />
            </div>
            <a href="#" className="link">
              Messages
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={bookmark} alt=""/> */}
              <Bookmark className="link-svg" />
            </div>
            <a href="#" className="link">
              Bookmarks
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={list} alt=""/> */}
              <List className="link-svg" />
            </div>
            <a href="#" className="link">
              Lists
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={profile} alt=""/> */}
              <Profile className="link-svg" />
            </div>
            <a href="#" className="link">
              Profile
            </a>
          </li>
          <li className="link-group-item">
            <div className="link-icon">
              {/* <img src={more} alt=""/> */}
              <More className="link-svg"></More>
            </div>
            <a href="#" className="link">
              More
            </a>
          </li>
        </ul>
      </div>
      <div className="btn-holder">
        <button className="btn">Tweet</button>
      </div>
      <div className="profile-holder" onClick={() => setisOpen(!isOpen)}>
        <div className="profile-img">
          <img src={user} alt="asdaasdasdasdasdasdasd" />
        </div>
        <div className="profile-details">
          <div className="profile-name">Maxwell Shaznokovich</div>
          <div className="profile-handle">@jdoe</div>
        </div>
        <div className="show-more">...</div>
        {isOpen && <div className="drop-down-menu">
          <div className="account">
            <div className="account-img">
              <img src={user} alt="asdaasdasdasdasdasdasd" />
            </div>
            <div className="account-details">
              <div className="account-name">Maxwell Shaznokovich</div>
              <div className="account-handle">@jdoe</div>
            </div>
            <div className="account-show-more">...</div>
          </div>
          <ul className="list">
            <li className="list-group">Add an existing account</li>
            <li className="list-group">Log out @jdoe</li>
          </ul>
        </div>}
      </div>
    </div>
  );
};

export default Index;
