import React from "react";

import "./index.scss";
import {ReactComponent as Magnifier} from "../../../../assets/icons/lnr-magnifier.svg";

const Index = () => {
  return (
    <div className="search-box">
      <div className="search-input">
        <div className="search-icon">
          <Magnifier className="magnifier" />
        </div>
        <input className="search-twitter" placeholder="Search Twitter"type="text" />
      </div>
    </div>
  );
};

export default Index;
