import React from "react";
import "./index.scss";
// import user from "../../../../../assets/user.jpg";

import {ReactComponent as Comment} from "../../../../../assets/icons/comment.svg";
import {ReactComponent as Heart} from "../../../../../assets/icons/heart.svg"
import {ReactComponent as Retweet} from "../../../../../assets/icons/retweet.svg"
import {ReactComponent as Export} from "../../../../../assets/icons/export.svg"

const Index = ({tweet}) => {
  return (
    <div className="tweet-card">
      <div className="tweet-img">
        <img src={tweet.User.avatarUri} alt="" />
      </div>
      <div className="tweet-body">
        <div className="tweet-user">
          <span className="tweet-username">{tweet.User.fullname}</span>
          <span className="tweet-handle">@{tweet.User.handle}</span>
        </div>
        <div className="tweet-content">
          {tweet.content}
        </div>
        <div className="tweet-buttons">
          <div className="button-holder">
            <div className="icon-holder">
              <Comment className="button-icon" />
            </div>
            <p className="icon-text">1</p>
          </div>
          <div className="button-holder">
            <div className="icon-holder">
              <Retweet className="button-icon" />
            </div>
            <p className="icon-text">1</p>
          </div>
          <div className="button-holder">
            <div className="icon-holder">
              <Heart className="button-icon" />
            </div>
            <p className="icon-text">1</p>
          </div>
          <div className="button-holder">
            <div className="icon-holder">
              <Export className="button-icon" />
            </div>
          </div>
        </div>
      </div>
      <div className="more-icon">...</div>
    </div>
  );
};

export default Index;
